.nav .container {
    display: flex;
    /* align-items: center; */
    justify-content: space-between;
}

.nav-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* margin-left: auto */
    margin-left: 60px;
}

.nav {
    padding: 50px 50px 50px 50px;
    padding-bottom: 20px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
}

.header {
    position: relative;
}



.nav-item {
    margin: 0px 45px;
}

.nav-item:nth-child(6) {
    margin-right: 0;
}

.nav-link {
    font-style: normal;
    font-weight: 400;
    font-size: 15.75px;
    line-height: 18px;
    color: #FFFFFF;
    transition: 0.3s;
    display: block;
    position: relative;
    padding-bottom: 8px;
}

.nav-links {
    font-style: normal;
    font-weight: 400;
    font-size: 15.75px;
    line-height: 18px;
    color: #FFFFFF;
}


.nav-logo img {
    max-width: 100px;
    min-width: 100px;
    max-height: 100px;
    min-height: 100px;
}


.nav-link::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    transform: scaleX(0);
    background: #edf0ef;
    border-radius: 23px 10px 0px 0px;
    transition: 0.3s;
}

.nav-link:hover::before {
    transform: scaleX(1);
}

.languages {
    position: relative;
}

.languages .lang-title {
    color: white;
    border: solid 1px white;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}

.d-none {
    display: none;
}

.languages .lang-menu {
    position: absolute;
    top: 35px;
    left: -10%;
    border-radius: 4px;
    overflow: hidden;
    background-color: white;
    width: 120%;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}

.lang {
    padding: 3px 0;
    text-align: center;
    cursor: pointer;
}

.lang:hover {
    background-color: black;
    color: white;
}

.header-bottom .container {
    display: flex;
    align-items: center;
}

.header {
    background-color: #16110B;
    height: 100vh;
    /* overflow: hidden; */
}

.header-title {
    font-size: 70.7434px;
    line-height: 70px;
    text-transform: uppercase;
    color: #FFFFFF;
    width: 570px;
    text-overflow: ellipsis;
    white-space: wrap;
    overflow: hidden;
    /* position: absolute; */
    height: 210px;
    /* background-color: red; */
    letter-spacing: 1px;
    font-family: intg1;
    font-weight: 600;
}

.header-text {
    /* background-color: yellow; */
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 125%;
    color: #FFFFFF;
    margin-top: 25px;
    text-overflow: ellipsis;
    white-space: wrap;
    overflow: hidden;
    height: 58px;
    width: 290px;
    font-family: 'Oswald', sans-serif;
    font-weight: 300;
}

.header-left {
    margin-top: 20vh;
    position: sticky;
    z-index: 2;
}

.header-bottom {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.header-right {
    position: absolute;
    left: 55.5%;
    bottom: -10px;
    /* z-index: 2  ; */
}

.header-bottom {
    padding: 80px 0;
    padding-top: 45px;
}

.header-img {
    width: 500px;
    height: 448.5px;
    position: sticky;
    z-index: 3;
}

@media (max-width:700px) {
    .header-img{
        z-index: 0;
    }
}

.btns {
    margin-top: 4vw;
    display: flex;
    align-items: center;
    justify-content: start;
}

.btns .explore {
    border: none;
    cursor: pointer;
    outline: none;
    margin-right: 20px;
    background-color: white;
    color: black;
    font-size: 16px;
    border: solid 1px white;
    padding: 13px 30px;
    font-weight: 800;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
    font-family: gilroy2;
    font-weight: 800;
}

.btns .explore:nth-child(2){
    background: linear-gradient(30deg, rgb(255, 200, 60), rgb(191, 73, 0));
}

.btns .explore:hover {
    background-color: black;
    color: white;
}
.btns .explore:nth-child(2):hover {
    background: black;
    color: white;
}

@media (max-width:500px) {
    .btns .explore {
        font-size: 14px !important;
    }
}

.menu-btn {
    background-color: transparent;
    border: none;
    display: none;
}

.modal-link {
    display: flex;
    align-items: center;
}

.modal-call {
    width: 30px;
    height: 35px;
    background-color: none;
    margin-right: 5px;
}

.modal-img {
    width: 25px;
    height: 25px;
}

.modal-list {
    list-style: none;
    margin-top: 55px;
}

.modal-link {
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    font-weight: 500;
}

.form-buttons {
    width: 40px;
    color: #FFFFFF;
    background-color: transparent;
    border: none;
    position: absolute;
    left: 73%;
    top: 3%;
    height: 40px;
    font-size: 45px;
}

.modal-item {
    margin: 10px 0;
}

.modal-btn {
    border: none;
    outline: none;
    cursor: pointer;
    position: absolute;
    left: 40%;
    margin-top: 20px;
    background-color: transparent;
}

@media (max-width:1350px) {

    .container {
        width: 1180px;
    }

    .header-img {
        width: 485px;
        height: 365.5px;
    }

}

@media (max-width:1160px) {

    .container {
        width: 1000px;
    }

    .nav-item {
        margin: 0px 32px;
    }
}

@media (max-width:1020px) {

    .container {
        width: 900px;
    }

    .header-img {
        width: 459px;
    }

    .header-right {
        position: absolute;
        top: 58%;
        left: 46.5%;
    }

    .header-bottom {
        padding-bottom: 0;
        margin-bottom: 0;
    }
}

@media (max-width:920px) {

    .container {
        width: 800px;
    }

    .nav-item:nth-child(6) {
        display: none;
    }

    .header-img {
        width: 344px;
    }
}



@media (max-width:820px) {

    .container {
        width: 700px;
    }

    .header-img {
        width: 274px;
    }

    .header-right {
        position: absolute;
        top: 65%;
        left: 56.5%;
    }

    .nav-item {
        margin: 0px 28px;
    }
}

@media (max-width:720px) {

    .container {
        width: 600px;
    }


    .header-right {
        top: auto;
        left: 28%;
    }

    .header-left {
        top: 0%;
        position: absolute;
        margin-top: 200px;
    }

    .menu-btn {
        display: block;
    }

    .nav-list {
        display: none;
    }

    .nav .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

@media (max-width:620px) {

    .container {
        width: 500px;
    }

    .header-title {
        font-size: 35px;
        line-height: 40px;
        width: 300px;
        height: 120px;
    }

    .header-text {
        font-size: 14px;
        line-height: 14px;
        height: 40px;
        width: 240px;
        margin-top: 5px;
    }

    .header-img {
        width: 250px;
        object-fit: contain;
    }


}



@media (max-width:520px) {

    .header {
        height: 630px;
    }

    .container {
        width: 400px;
    }

    .header-bottom {
        top: 15%;
    }

    .header-right {
        top: auto;
        left: 25%;
    }

    .header-left {
        margin-top: 30px;
        position: absolute;
        top: 0%;
        left: 20%;
    }

    .header-title {
        font-size: 38px;
        line-height: 40px;
        width: 340px;
        height: 120px;
    }

    .nav .container {
        display: flex;
        justify-content: space-around;
    }

    .menu-btn {
        margin-left: -80px;
    }

    .nav-logo {
        margin-left: -130px;
    }

}

@media (max-width:420px) {

    .container {
        width: 340px;
    }

    .header-title {
        font-size: 23px;
        line-height: 30px;
        width: 260px;
        height: 120px;
    }

    .header-right {
        top: auto;
        left: 20%;
    }

    .header-left {
        left: 20%;
    }

    .nav .container {
        width: 300px;
        display: flex;
        justify-content: space-between;
        margin-left: 30px;
    }

    .nav-logo {
        margin: 0;
        padding: 0;
    }
}