* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
}

@media (max-width:500px) {
  .btns .explore {
    font-size: 14px;
  }
}

.texture1 {
  position: absolute;
  width: 25vw;
  height: 30vw;
  background-color: rgba(227, 177, 101, 0.394);
  filter: blur(150px);
  bottom: 10%;
  left: 0;
  z-index: 0;
  -webkit-filter: blur(150px);
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

