body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  scroll-behavior: smooth;
}

.container {
  width: 1140px;
  margin: 0 auto;
}

.loading {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 7vw;
  color: black;
}

.loader {
  position: fixed;
  background-color: #16110B;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 77777777777;
  transition: 0.3s;

}
.loader .load {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  border: solid 10px #16110B;
  border-left-color: rgb(255, 179, 0);
  border-top-color: rgb(255, 179, 0);
  animation-name: loader;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.loader.opacity-load {
  animation-name: parent-load;
  animation-duration: 2s;
  animation-timing-function: linear;
}

@keyframes loader {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes parent-load {
  90% {
    opacity: 0;
  }

  100% {
    display: none;
  }
}