.Modal {
    top: 25%;
    left: 25%;
    margin: 0;
    width: 50%;
    height: 50%;
    padding: 2.5vw;
    display: flex;
    position: fixed;
    z-index: 20002;
    border-radius: 2vw;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background-color: black;
}

h1 {
    color: white;
    font-size: 3vw;
    font-weight: 700;
    margin-bottom: 2%;
    text-align: center;
    font-family: 'Oswald', sans-serif;
    font-family: 600;
}

button {
    border: 0;
    outline: none;
    color: black;
    margin-top: 2%;
    font-weight: 700;
    font-size: 1.5vw;
    padding: 10px 50px;
    border-radius: 0.8vw;
    font-family: 'Oswald', sans-serif;
    font-family: 600;
    background-color: rgba(255, 255, 255, 0.73);

}

@media(max-width:1200px) {
    .Modal button {
        font-size: 2vw;
    }

    .Modal h1 {
        font-size: 3.5vw;
    }
}

@media(max-width:800px) {
    .Modal {
        top: 35%;
        left: 20%;
        width: 60%;
        height: 30%;
        padding: 2vw;
    }

    .Modal button {
        font-size: 2.5vw;
    }

    .Modal h1 {
        font-size: 4vw;
    }
}

@media(max-width:500px) {
    .Modal {
        top: 35%;
        left: 10%;
        width: 80%;
        height: 200px;
    }

    .Modal button {
        font-size: 4vw;
        padding: 8px 40px;
    }

    .Modal h1 {
        font-size: 5.5vw;
    }
}