.ScrollPercent {
    right: 2%;
    bottom: 3%;
    width: 4vw;
    height: 4vw;
    z-index: 101;
    display: flex;
    position: fixed;
    cursor: pointer;
    transition: 0.3s;
    border-radius: 50%;
    align-items: center;
    text-decoration: none;
    justify-content: center;
}

.fa-angle-up {
    margin: 0;
    padding: 0;
    width: 88%;
    height: 88%;
    display: flex;
    font-size: 3vw;
    color: black;
    font-weight: 100;
    border-radius: 50%;
    position: absolute;
    justify-content: center;
    background-color: #ffa647;
}

@media(max-width:800px) {
    .ScrollPercent {
        width: 7vw;
        height: 7vw;
    }

    .fa-angle-up {
        font-size: 5vw;
    }
}

@media(max-width:500px) {
    .ScrollPercent {
        right: 4%;
        bottom: 2%;
        width: 9vw;
        height: 9vw;
    }

    .fa-angle-up {
        font-size: 7vw;
    }
}

.bottom {
    bottom: -10vw;
    transition: 0.3s;
}