.aside-name{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 28.7306px;
    line-height: 130%;
    color: rgba(255, 255, 255, 0.9);
    font-family: intg2;
    font-weight: 400;
}

.aside{
    /* background-image: url("../../Assets/Img/background.png"); */
    background-repeat: no-repeat, repeat;
    background-size: 100% 100%;
    /* object-fit: cover; */
    background-position: center;
    position: relative;
    padding: 100px 0;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),;
}
.back-img1{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    object-fit: cover;
}
.back-img2{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    z-index: -1;
}
@media (max-width:600px) {
    .back-img2{
        display: none;
    }
}

.aside-iframe{
    width: 828px;
    height: 318px;
    margin-top: 20px;
    border-radius: 33px;
    border:4px solid #AE6E1F;
}

.aside-names{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    margin-top: 30px;
    color: rgba(255, 255, 255, 0.9);
    font-family: gilroy2;
    font-weight: 600;
}

.aside-text{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    margin-top: 30px;
    width: 230px;
    text-overflow: ellipsis;
    white-space: wrap;
    overflow: hidden;
    font-size: 14px;
    line-height: 25px;
    color: rgba(255, 255, 255, 0.55);
    font-family: gilroy2;
    font-weight: 600;
}


@media (max-width:1350px) {
    
    .container{
        width: 1180px;
    }
}

@media (max-width:1160px) {
    
    .container{
        width: 1000px;
    }
}

@media (max-width:1020px) {
    
    .container{
        width: 900px;
    }
}

@media (max-width:920px) {
    
    .container{
        width: 800px;
    }
    .aside-iframe{
        width: 700px;
    }
}

@media (max-width:820px) {
    
    .container{
        width: 700px;
    }

    .aside-iframe{
        width: 600px;
    }
}

@media (max-width:720px) {
    
    .container{
        width: 600px;
    }

    .aside-iframe{
        width: 510px;
    }
}

@media (max-width:620px) {
    
    .container{
        width: 500px;
    }

    .aside-iframe, .aside-map{
        /* width: 450px; */
        height: 250px;
    }

    .aside .container{
        width: 500px;
    }
}

@media (max-width:520px) {
    
    .container{
        width: 400px;
    }
    .aside-iframe{
        width: 380px;
        height: 200px;
    }

    .aside-name{
        font-size: 24px;
        line-height: 130%;
    }

    .aside .container{
        width: 400px;
    }
    .aside{
        padding: 40px 0;
    }
}

@media (max-width:420px) {
    
    .container{
        width: 340px;
    }

    .aside-iframe{
        width: 330px;
        height: 180px;
    }
    .aside-name{
        font-size: 20px;
        line-height: 130%;
    }

    .aside .container{
        width: 340px;
        /* background-color: red; */
    }
}