.box{
    background-color: #060403;
    padding: 180px 0;
    position: relative;
}
.box-name{
    font-size: 134.452px;
    line-height: 103.17%;
    color: #fff;
    font-family: 'Josefin Sans';
    text-align: center;
    margin-top: 30px;
}

.box-imgs{
    text-align: center;
    position: absolute;
    top: 31%;
    left: 45%;
    transform-style: preserve-3d;
}
.box-list{
    display: flex;
    list-style: none;
    margin-top: -25px;
    flex-wrap: wrap;
    justify-content:center;
}

.box-text{
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 125%;
    color: #fff;
    /* width: 340px; */
    margin: 20px 40px;
    text-overflow: ellipsis;
    white-space: wrap;
    overflow: hidden;
}

.box-pic{
    margin-top: 20px;
}

@media (max-width:1350px) {
    
    .container{
        width: 1180px;
    }
}

@media (max-width:1160px) {
    
    .container{
        width: 1000px;
    }
}

@media (max-width:1020px) {
    
    .container{
        width: 900px;
    }

    .box-imgs{
        left: 43%;
    }
}

@media (max-width:920px) {
    
    .container{
        width: 800px;
    }

    .box-text{
        margin: 20px 15px;
        font-size: 13px;
        line-height: 110%;
        width: 320px;
    }

    .box-name{
        font-size: 120px;
        line-height: 103.17%;
    }
    .box-list{
        margin-top: 20px;
    }
    
    .box-imgs{
        left: 42%;
    }
    .box-pic{
        margin-top: 0;
    }
}

@media (max-width:820px) {
    
    .container{
        width: 700px;
    }
    .box-name{
        font-size: 100px;
        line-height: 103.17%;
    }

    .box-text{
        margin: 20px 10px;
        font-size: 10px;
        line-height: 110%;
        width: 250px;
    }
    .box-imgs{
        left: 40%;
        top: 32%;
    }
}

@media (max-width:720px) {
    
    .container{
        width: 600px;
    }

    .box-name{
        font-size: 80px;
        line-height: 103.17%;
        width: 500px;
        margin:  0 auto;
    }

    .box-text{
        margin: 20px 25px;
        font-size: 10px;
        line-height: 14px;
        width: 200px;
    }

    .box-img{
        width: 100px;
    }

    .box-imgs{
        left: 41%;
        top: 30%;
    }
}



@media (max-width:620px) {
    
    .container{
        width: 500px;
    }

    
    .box-imgs{
        left: 43%;
        top: 18%;
    }

    .box-list{
        flex-wrap: wrap;
        display: flex;
        justify-content: center;
    }

    .box-name{
        font-size: 60px;
        line-height: 103.17%;
    }

    .box-text{
        margin: 20px 30px;
        font-size: 12px;
        line-height: 16px;
        width: 260px;
    }

    
    .box-img{
        width: 80px;
    }

    .box-pic{
        height: 50px;
        width: 110px;
    }

    .box .container{
        width: 500px;
    }
}




@media (max-width:520px) {
    
    .container{
        width: 400px;
    }
    .box-list{
        flex-wrap: wrap;
        display: flex;
        justify-content: center;
    }

    .box-name{
        width: 360px;
        margin-left: 50px;
    }
    .box-text{
        margin: 20px 20px;
        font-size: 14px;
        line-height: 18px;
        width: 280px;
    }
    .box .container{
        width: 400px;
    }
}

@media (max-width:420px) {
    .box{
        padding: 50px 0;
    }
    
    .container{
        width: 340px;
    }

    .box-imgs{
        left: 37%;
        top: 20%;
    }

    .box-name{
        width: 300px;
        font-size: 55px;
        margin-left: 30px;
    }
    .box .container{
        width: 340px;
    }

    .box-text{
        margin: 20px 10px;
        font-size: 12px;
        line-height: 18px;
        width: 280px;
    }
    .box-pic{
        height: 70px;
        width: 150px;
        object-fit: cover;
    }
}

.About{
    background-color: #110D0A;
    padding: 50px 0;
    position: relative;
    overflow: hidden;
}
.back-img{
    position: absolute;
    height: 100%;
    top: 0;
    right: 50px;
    z-index: 0;
}
.title{
    color: white;
    font-size: 10vw;
    text-align: center;
    font-family: intg1;
    font-weight: 600;
    position: sticky;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}
.gold{
    width: 14vw;
    position: absolute;
    bottom: -5vw;
    filter: drop-shadow(10px 40px 25px rgb(0, 0, 0));
    -webkit-filter: drop-shadow(10px 40px 25px rgb(0, 0, 0));
}
.texts{
    /* margin-top: 2vw; */
    display: flex;
    flex-wrap: wrap;
    padding: 0 10vw;
    position: sticky;
    z-index: 2;
}

.texts p{
    color: white;
    width: 50%;
    padding: 2vw;
    font-family: gilroy2;
    font-weight: 600;
}

@media (max-width:900px) {
    .texts{
        padding: 0 7vw;
    }
    .texts p{
        font-size: 14px;
    }
}
@media (max-width:600px) {
    .texts{
        padding: 0 7vw;
        margin-top: 6vw;
    }
    .texts p{
        width: 100%;
    }
    .back-img{
        display: none;  
    }
    .gold{
        width: 20vw;
        bottom: -9vw;
    }
    .sap-img{
        max-width: 30vw;
    }
}