.footer{
    background-color: #010100;
    padding: 100px 0;
    position: relative;
}

.footer-imgs{
    position: absolute;
    top: 24%;
    left: 76%;
}

.footer-name{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 700;
    font-size: 31.5594px;
    line-height: 32px;
    color: #fff;
    width: 300px;
    font-family: intg2;
    font-weight: 400;
}

.footer-text{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #fff;
    width: 370px;
    margin: 20px 0;
    font-family: gilroy2;
    font-weight: 600;
}

.footer-page{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.footer-form{
    display: flex;
    flex-direction: column;
}

.footer-input{
    filter: drop-shadow(0px 1.52128px 8.36702px rgba(69, 65, 164, 0.06)) drop-shadow(0px 3.04255px 7.60638px rgba(31, 37, 89, 0.07));
    border-radius: 10px;
    outline: none;
    border: none;
    background-color: #010100;
    padding: 15px 8px;
    color: white;
    border: 1px solid white;
    width: 100%;
}

.footer-names{
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 15.6915px;
    line-height: 15px;
    color: #fff;
    margin-bottom: 10px;
    font-family: gilroy2;
    font-weight: 600;
}
.footer-surname{
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 13.6915px;
    line-height: 15px;
    color: #A06304;
    margin-top: 30px;
    margin-left: 20px;
    font-family: gilroy2;
    font-weight: 600;
}

.footer-right{
    margin: 0 40px;
    margin-top: 30px;
}


.footer-inputs{
    width: 210px;
    display: flex;
    border-radius: 10px;    
    align-items: center;
    color: white;
    /* border: solid 1px white; */
}

.footer-input::placeholder{
    font-style: normal;
    font-weight: 500;
    font-size: 13x;
    line-height: 15px;
    color: white;
    font-family: gilroy2;
    font-weight: 600;
}
.footer-input:hover{
    color: #cb7d00;
    border: solid 2px #cb7d00;
}
.footer-input:hover::placeholder{
    color: #cb7d00;
}
.footer-input:focus{
    color: #cb7d00;
    border: solid 2px #cb7d00;
}
.footer-input:focus::placeholder{
    color: #cb7d00;
}

.footer-phone{
    border: 1px solid white;
    filter: drop-shadow(0px 1.52128px 4.56383px rgba(19, 18, 66, 0.07));
    border-radius: 10px;
    width: 210px;
    padding: 15px 8px;
    color: #fff;
    outline: none;
    background-color: transparent;
    /*border: none;
    */resize: none;
    color: white;
    -webkit-filter: drop-shadow(0px 1.52128px 4.56383px rgba(19, 18, 66, 0.07));
}

.footer-phone::placeholder{
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
    color: white;
    font-family: gilroy2;
    font-weight: 600;
}
.footer-phone:hover{
    color: #cb7d00;
    border: solid 2px #cb7d00;
}
.footer-phone:hover::placeholder{
    color: #cb7d00;
}
.footer-phone:focus{
    color: #cb7d00;
    border: solid 2px #cb7d00;
}
.footer-phone:focus::placeholder{
    color: #cb7d00;
}
.footer-labels{
    display: flex;
    align-items: center;
}

.footer-label{
    margin: 0 10px;
}

.footer-left{
    margin: 0 40px;
}

.footer-texts{
    border: 1px solid white;
    filter: drop-shadow(0px 1.52128px 4.56383px rgba(19, 18, 66, 0.07));
    border-radius: 10px;
    width: 440px;
    margin-left: 15px;
    margin-top: 20px;
    outline: none;
    color: #fff;
    height: 150px;
    padding: 10px 20px 3px;
    resize: none;
    background-color: transparent;
    font-family: gilroy2;
    font-weight: 600;
    color: white;
}

.footer-texts::placeholder{
    font-style: normal;
    font-weight: 500;
    font-size:13px;
    line-height: 15px;
    color: white;
    font-family: gilroy2;
    font-weight: 600;
}
.footer-texts:hover{
    color: #cb7d00;
    border: solid 2px #cb7d00;
}
.footer-texts:hover::placeholder{
    color: #cb7d00;
}
.footer-texts:focus{
    color: #cb7d00;
    border: solid 2px #cb7d00;
}
.footer-texts:focus::placeholder{
    color: #cb7d00;
}

.footer-btn{
    border: 1px solid #ffffff;
    filter: drop-shadow(0px 6.08511px 16.734px rgba(74, 58, 255, 0.26));
    border-radius: 20px;
    background-color: transparent;
    width: 135px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16.6915px;
    line-height: 15px;
    color: #ffffff;
    margin-top: 30px;
    margin-left: 13px;
    padding: 20px 0;
    cursor: pointer;
    font-family: gilroy2;
    font-weight: 600;
}

.footer-btn:hover{
    color: #cb7d00;
    border: solid 1px #cb7d00;
}

.footer-box{
    display: flex;
    margin-top: 25px;
    margin-left: 40px;
}

.PhoneInputInput{
    border: none;
    color: #fff;
    outline: none;
    background-color: transparent;
}



.footer-textss{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 25px;
    color: rgba(255, 255, 255, 0.55);
    width: 320px;
    margin-left: 18px;
    margin-top: 20px;
    font-family: gilroy2;
    font-weight: 600;
}

.footer-surname{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: rgba(255, 255, 255, 0.9);
    font-family: gilroy2;
    font-weight: 600;
}

.footer-icon{
    margin-left: 7px;
    margin-top: 20px;
}

.footer-logo{
    margin: 0 8px;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.footer-logo:hover{
    transform: scale(1.4);
    -webkit-transform: scale(1.4);
    -moz-transform: scale(1.4);
    -ms-transform: scale(1.4);
    -o-transform: scale(1.4);
}

.footer-message{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    margin-left: 15px;
    color: rgba(255, 255, 255, 0.55);
    font-family: gilroy2;
    font-weight: 600;
}

.footer-list{
    margin: 0 220px;
    padding: 0;
}

.footer-item{
    margin: 10px;
    margin-left: 15px;
}

@media (max-width:1350px) {
    
    .container{
        width: 1180px;
    }

    .footer-imgs{
        left: 82%;
    }
}

@media (max-width:1160px) {
    
    .container{
        width: 1000px;
    }
}

@media (max-width:1020px) {
    
    .container{
        width: 900px;
    }
}

@media (max-width:920px) {
    
    .container{
        width: 800px;
    }

    .footer-texts{
        width: 370px;
    }
    .footer-phone{
        width: 180px;
    }
    .footer-inputs{
        width: 180px;
    }

    .footer-input{
        width: 150px;
    }
    .footer-imgs{
        height: 240px;
        top: 44%;
        left: 68%;
    }
    .footer-left, .footer-right{
        margin: 0 20px;
    }

    .footer-page{
        display: flex;
        flex-direction: column;
    }

    .footer-list{
        margin: 0 100px;
    }
}

@media (max-width:720px) {
    
    .container{
        width: 600px;
    }

    .footer .container{
        width: 600px;
    }

    .footer-list{
        margin: 0 50px;
    }

    .footer-textss{
        font-size: 12px;
        line-height: 18px;
        width: 260px;
    }
}

@media (max-width:620px) {
    
    .container{
        width: 500px;
    }

    .footer .container{
        width: 500px;
    }

    .footer-message{
        font-size: 14px;
        line-height: 20px;
    }
    .footer-left{
        width: 100%;
        /* border: solid 1px white; */
        padding: 0 10px;
    }

    .footer-list{
        margin: 0 40px;
        /* border: solid 1px white;         */
    }
    .footer-item{
        padding: 0;
        margin: 10px 0;
        /* border: solid 1px white; */
    }

    .footer-name{
        font-size: 27px;
        line-height: 32px;
        /* width: 230px; */
    }

    .footer-text{
        font-size: 12px;
        line-height: 15px;
        width: 290px;
    }

    .footer-box{
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0 10px;
    }
    .footer-list{
        margin: 0;              
    }        
}



@media (max-width:520px) {
    
    .container{
        width: 400px;
    }

    .footer .container{
        width: 400px;
    }
    

    .footer-title{
        width: 330px;
    }
    .footer{
        padding: 40px 0;
    }
}

@media (max-width:450px) {
    
    .container{
        width: 340px;
    }

    
    .footer-title{
        width: 300px;
        margin-left: -10px;
    }

    .footer .container{
        width: 340px;
    }
    .footer-texts{
        width: 310px;
        height: 140px;
    }

    .footer-inputs{
        width: 150px;
    }
    .footer-input{
        width: 150px;
    }

    .footer-phone{
        width: 150px;
    }

    .footer-imgs{
        height: 200px;
        top: 32%;
        left: 70%;
        width: 90px;
    }

    .footer-btn{
        width: 110px;
        padding: 15px 0;
    }
}