.section{
    background-color: #060403;
    padding: 170px 0;
    position: relative;
    background-image: url("../../Assets/Img/S.png");
    background-repeat: no-repeat;
    background-position: top 20% left 0;
    overflow: hidden;
}
.btns{
    margin-top: 4vw;
    display: flex;
    align-items: center;
    justify-content: start;
}
.sec-btn{
    justify-content: end !important;
}

.btns .explore{
    border: none;
    cursor: pointer;
    outline: none;
    margin-right: 20px;
    background-color: white;
    color: black;
    font-size: 16px;
    border: solid 1px white;
    padding: 13px 30px;
    font-weight: 600;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
    font-family: gilroy2;
    font-weight: 600;
}
.btns .explore:hover{
    background-color: black;
    color: white;
}



.section-right{
    position: absolute;
    top: 30%;
    left: 59%;
    width: 11.5vw;
    height: 17vw;
}
.section-right img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.section-right1{
    position: absolute;
    top: 36%;
    right: 14%;
    width: 12vw;
    height: 17vw;
}
.section-right1 img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.section-left{
    margin-left: 50px;
}

.section-name{
    font-family: 'Josefin Sans';
    font-size: 71.1215px;
    line-height: 62px;
    text-transform: uppercase;
    color: #fff;
    width: 800px;
    font-weight: 700;
    font-family: intg1;
    font-weight: 600;
}

.section-text, .section-texts{
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 125%;
    color: #fff;
    width: 340px;
    margin: 20px 0;
    text-overflow: ellipsis;
    white-space: wrap;
    overflow: hidden;
    font-family: gilroy2;
    font-weight: 600;
}

.section-box{
    background-color: #110D0A;
    padding: 180px 0;
    position: relative;
    background-repeat: no-repeat;
    background-position: top 10% left 80%;
    background-image: url("../../Assets/Img/e.png");
}

.section-names{
    font-size: 71.1215px;
    line-height: 62px;
    text-align: right;
    text-transform: uppercase;
    color: #FFFFFF;
    font-family: 'Josefin Sans';
    font-weight: 800;
    width: 700px;
    margin-left: 70px;
    font-family: intg1;
    font-weight: 600;
    /* margin-top: -20px; */
}

.section-texts{
    margin-left: 430px;
    font-family: gilroy2;
    font-weight: 600;
}

.section-box{
    position: relative;
}

.section-rights{
    position: absolute;
    top: 30%;
    left: 0%;
}

.section-pic{
    width: 800px;
}

@media (max-width:1450px) {
    .container{
        width: 800px;
    }
}
@media (max-width:1350px) {
    
    .section-box .container{
        /* width: 800px; */
    }    
}

@media (max-width:1160px) {
    
    .container{
        width: 1000px;
    }
}

@media (max-width:1020px) {
    
    .container{
        width: 900px;
    }

    .section-right{
        position: absolute;
        top: 55%;
        left: 53%;
    }
    .section-right1{
        position: absolute;
        top: 55%;
        right: 23%;
    }

    .section-img{
        width: 550px;
    }
}

@media (max-width:920px) {
    .container{
        width: 800px;
    }

    .section-name{
        font-size: 55px;
        line-height: 50px;
        width: 650px;
    }

    .section-names{
        font-size: 52px;
        width: 600px;
        line-height: 55px;
    }

    .section-pic{
        width: 700px;
    }
}

@media (max-width:820px) {
    .container{
        width: 700px;
    }
    
    .section-texts{
        font-size: 13px;
        line-height: 110%;
        width: 280px;
    }
    .section-pic{
        width: 660px;
    }
    .section-img{
        width: 500px;
    }
}

@media (max-width:720px) {
    
    .container{
        width: 600px;
    }

    .section-name{
        font-size: 40px;
        line-height: 50px;
        width: 470px;
    }

    .section-img{
        width: 400px;
    }   

    .section-names{
        font-size: 40px;
        width: 470px;
        line-height: 50px;
    }

    .section-texts{
        font-size: 13px;
        line-height: 110%;
        width: 280px;
        margin-left: 300px;
    }

    .section-pic{
        width: 580px;
    }
}

@media (max-width:620px) {

    .container{
        width: 500px;
    }
    .section{
        background-image: none;
    }
    .section-box{
        background-image: none;
    }

    .section-pic{
        width: 480px;
    }

    .section-img{
        width: 300px;
        height: 250px;
    }

    .section-texts{
        font-size: 10px;
        line-height: 110%;
        width: 230px;
        margin-left: 280px;
    }

    .section-right{
        position: absolute;
        top: 47%;
        left: 47%;
    }
    .section-text{
        font-size: 14px;
        line-height: 110%;
        width: 230px;
    }

    .section-name{
        font-size: 35px;
        line-height: 40px;
        width: 380px;
    }
}

@media (max-width:520px) {
    .container{
        width: 400px;
    }

    .section-right{
        top: 67%;
        left: 52%;
    }
    .section-right1{
        top: 60%;
        right: 12%;
    }
    .section{
        padding: 220px 0;
    }
    .section-left{
        top: 20%;
        position: absolute;
    }

    .section-pic{
        width: 100%;
    }

    .section-rights{
        top: 15%;
    }

    .section-lefts{
        top: 35%;
        /* left: 14%; */
        position: absolute;
    }

    .section-names, .section-name{
        font-size: 40px;
    }

    .section-names{
        width: 380px;
    }

    .section-name{
        width: 330px;
    }
    .section-texts{
        margin-left: 200px;
    }
    .section-rights{
        display: none;
    }
}

@media (max-width:450px) {
    
    .container{
        width: 340px;
    }
    .section{
        padding: 190px 0;
    }

    .section-img{
        width: 260px;
    }

    .section-lefts{
        left: -4%;
        top: 30%;
    }

    .section-name{
        font-size: 23px;
        line-height: 30px;
        width: 220px;
    }

    .section-names{
        font-size: 25px;
        line-height: 30px;
        width: 100%;
        padding: 0 13vw;
    }

    .section-texts{
        /* margin-left: 90px; */
        width: 100%;
        padding: 0 13vw;
        margin: 0;
        font-size: 14px;
        margin-top: 4vw;
        text-align: right;
    }
    .section-box .explore{
        margin-right: 0 !important;
    }

    .section-left{
        left: 0%;
    }

    .section-right{
        left: 55%;
    }
    .section-right1{
        right: 15%;
    }
}