.Loader{
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    z-index: 1001;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;
    background-color: white;
}

.logo{
    font-size: 10vw;
    font-weight: 400;
    position: absolute;
    width: 100%;
    left: 0;
    text-align: center;
}

.percent{
    right: 0;
    width: 0;
    bottom: 10%;
    height: 5px;
    position: absolute;
    animation-name: line;
    animation-duration: 10s;
    background-color: black;
    animation-timing-function: linear;
}

.out-door{
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    position: absolute;
    align-items: center;
    animation-name: door;
    justify-content: center;
    animation-duration: 10s;
    background-color: black;
    animation-timing-function: linear;
}

.logo1{
    top: auto;
    left: 0;
    z-index: 2;
    width: 100%;
    color: white;
    font-size: 10vw;
    font-weight: 400;
    text-align: center;
    position: absolute;
    animation-name: logo;
    animation-duration: 10s;
    animation-timing-function: linear;
}


.percent1{
    right: 0;
    width: 0;
    bottom: 10%;
    height: 5px;
    position: absolute;
    animation-name: line1;
    animation-duration: 10s;
    background-color: white;
    animation-timing-function: linear;
}

@keyframes door {
    100%{
        right: -100%;
    }
}

@keyframes logo {
    100%{
        left: -100%;        
    }
}

@keyframes line {
    100%{
        width: 100%;         
    }
}

@keyframes line1 {
    100%{
        width: 100%; 
        right: 100%;        
    }
}

