.sublime {
    background-color: #060403;
    padding: 170px 0;
    position: relative;
    background-image: url("../../Assets/Img/v.png");
    background-repeat: no-repeat;
    background-position: top 2% left 0;
    overflow: hidden;
}

.sublime-name {
    font-family: 'Josefin Sans';
    font-size: 71.1215px;
    line-height: 62px;
    text-transform: uppercase;
    color: #fff;
    width: 800px;
    font-weight: 700;
    font-family: intg1;
    font-weight: 600;
}

.btns {
    margin-top: 4vw;
    display: flex;
    align-items: center;
    justify-content: start;
}

.btns .explore {
    border: none;
    cursor: pointer;
    outline: none;
    margin-right: 20px;
    background-color: white;
    color: black;
    font-size: 16px;
    border: solid 1px white;
    padding: 13px 30px;
    font-weight: 600;
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
    font-family: gilroy1;
    font-weight: 600;
}

.btns .explore:hover {
    background-color: black;
    color: white;
}

.sublime-text {
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 125%;
    color: #fff;
    width: 340px;
    margin: 20px 0;
    text-overflow: ellipsis;
    white-space: wrap;
    overflow: hidden;
    font-family: gilroy2;
    font-weight: 600;
}


.sublime .container {
    display: flex;
    justify-content: center;
}

.sublime-right {
    position: absolute;
    top: 20%;
    left: 65%;
}
.sublime-img{
    max-width: 25vw;
    min-width: 25vw;
}
@media (max-width:1450px) {

    .sublime-right {
        position: absolute;
        top: 30%;
        left: 70%;
    }
}
@media (max-width:1350px) {

    .container {
        width: 1180px;
    }
}

@media (max-width:1160px) {

    .container {
        width: 1000px;
    }

    .sublime-name {
        font-size: 62px;
        line-height: 60px;
        width: 680px;
    }
}

@media (max-width:1020px) {

    .container {
        width: 900px;
    }

    .sublime-img {
        width: 380px;
    }

    .sublime-name {
        font-size: 55px;
        line-height: 60px;
        width: 680px;
    }

    .sublime-right {
        position: absolute;
        top: 35%;
        left: 50%;
    }
}

@media (max-width:920px) {
    .container {
        width: 800px;
    }
}

@media (max-width:820px) {
    .container {
        width: 700px;
    }

    .sublime-img {
        width: 350px;
    }

    .sublime-right {
        position: absolute;
        top: 37%;
        left: 50%;
    }
}



@media (max-width:720px) {

    .container {
        width: 600px;
    }

    .sublime-name {
        font-size: 40px;
        line-height: 50px;
        width: 480px;
    }

    .sublime-right {
        /* background-color: red; */
        position: absolute;
        top: 0%;
        left: 35%;
    }

    .sublime-img {
        width: 310px;
    }

    .sublime-text {
        font-size: 12px;
        line-height: 110%;
        width: 280px;
    }
}

@media (max-width:620px) {

    .container {
        width: 500px;
    }

    .sublime {
        background-image: none;
    }

    .sublime-name {
        font-size: 35px;
        width: 400px;
        margin-right: 65px;
    }

    .sublime-text {
        font-size: 14px;
        line-height: 110%;
        width: 240px;
    }

    .sublime-img {
        width: 280px;
    }
}

@media (max-width:520px) {
    .sublime{
        padding: 250px 0;
    }
    .container {
        width: 400px;
    }

    .sublime-right {
        top: 55%;
        left: 35%;
    }

    .sublime-left {
        top: 8%;
        left: 15%;
        position: absolute;
    }

    .sublime-text {
        width: 250px;
    }

    .sublime-name {
        width: 350px;
    }
}

@media (max-width:420px) {

    .container {
        width: 340px;
    }

    .sublime-name {
        font-size: 25px;
        line-height: 30px;
        width: 220px;
    }

    .sublime-right {
        top: 55%;
        left: 35%;
    }
    .sublime-right img{
        max-width: 40vw;
        min-width: 40vw;
    }
}