.Navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 10px 10vw;
    z-index: 5;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.navbar-brand {
    width: 4vw;
    margin-right: 4vw;
}

@media (max-width:600px) {
    .navbar-brand {
        width: 7vw;
    }
}
@media (max-width:500px) {
    .navbar-brand {
        width: 10vw;
    }
}

.navbar-brand img {
    max-width: 100%;
    min-width: 100%;
}

.navbar-nav {
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.nav-item {
    list-style: none;
    padding: 0;
    margin: 0;
}

.nav-link {
    text-decoration: none;
    color: white;
    font-family: gilroy2;
    font-weight: 600;
}

.nav-link::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    transform: scaleX(0);
    background: #edf0ef;
    border-radius: 23px 10px 0px 0px;
    transition: 0.3s;
}

.nav-link:hover::before {
    transform: scaleX(1);
}

.languages {
    position: relative;
}

.languages .lang-title {
    color: black;
    border: solid 1px white;
    background-color: white;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    font-family: gilroy2;
    font-weight: 600;
}

.languages .lang-title:hover{
    color: white;
    background-color: rgba(0, 0, 0, 0.994);
}

.d-none {
    display: none;
}

.languages .lang-menu {
    position: absolute;
    top: 35px;
    left: -10%;
    border-radius: 4px;
    overflow: hidden;
    background-color: white;
    width: 120%;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}

.lang {
    padding: 3px 0;
    text-align: center;
    cursor: pointer;
}

.lang:hover {
    background-color: black;
    color: white;
}

@media (max-width:900px) {
    .navbar-nav {
        position: absolute;
        width: 100%;
        height: 100vh;
        background-color: black;
        top: 0;
        right: -100%;
        flex-direction: column;
        padding: 70px 0;
    }
}

.close-icon {
    position: absolute;
    top: 20px;
    right: 30px;
    color: white;
    font-size: 35px;
    cursor: pointer;
}

@media (min-width:900.1px) {
    .close-icon {
        display: none;
    }

    .open-icon {
        display: none;
    }
}

.navbar-nav.show {
    right: 0;
}

.nav-tel {
    padding: 7px 12px;
    background-color: white;
    border: solid 1px white;
    color: black;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: gilroy2;
    font-weight: 600;
}

.nav-tel:hover{
    color: white;
    background-color: rgb(0, 0, 0);
}
.nav-tel:hover .fa{
    color: white;
}
.nav-tel .fa {
    color: black;
    margin-right: 5px;
}

.black-nav{
    background-color: black;
}